<template>
	<view>
		<map
		   style="width: 100%; height: 100vh;"
		   id="myMap"
		   :longitude="long"
		   :latitude="lat"
		   :layer-style="3"
		   max-scale
		   :show-location="true"
		   :enable-building="true"
		 ></map>
	</view>
</template>

<script>
	export default{
		data(){
			return {
				long:104.73,
				lat :31.48
			}
		},
		onLoad(){
					uni.getLocation({//获取当前的位置坐标
						type: 'wgs84',
						success: function (res) {
							console.log('当前位置的经度：' + res.longitude);
							console.log('当前位置的纬度：' + res.latitude);
						}
					});  
				},
		methods:{
			
		}
	}
</script>

